* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    font-size: 18px;
    background-color: #d0c7c7;
}

.contenedor-principal {
    
    min-height: 600px;
    display: flex;
    flex-wrap: wrap;
    align-content: center;
    justify-content: center;
}

h1 {
    max-width: 940px;
    font-size: 2.5em;
    font-family: Lato, sans-serif;
    font-weight: 700;
    line-height: 1.1;
    margin: 40px 0;
}
